<template>
  <transition name="head-login-register">
    <div class="background">
      <br>
      <div class="register_block">
        <div class="register_head_admin">
          <p>管理员登录</p>
        </div>
        <el-form>
          <div id="register-name">
            <el-form-item>
              <el-input class="inputs" style="width: 290px" type="text" size="medium" placeholder="请输入您的管理员账号"
                        v-model="userName" clearable></el-input>
            </el-form-item>
          </div>
          <div id="register-password">
            <el-form-item>
              <el-input class="inputs" style="width: 290px" type="text" size="medium" placeholder="请输入您的密码"
                        v-model="userPassWord" show-password clearable></el-input>
            </el-form-item>
          </div>
          <div class="confirm-button">
            <el-button id="button_in_admin" type="success" size="medium" v-on:click="goToAdminHead">
              确认
            </el-button>
          </div>
          <div class="register-button">
            <el-button id="button_re" type="success" :plain="true" size="medium" v-on:click="goToStart">
              返回
            </el-button>
          </div>
        </el-form>
      </div>
      <div class="register_right_admin">
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'AdminLogin',
  data: function () {
    return {
      userNickName: '',
      userName: '',
      userPassWord: '',
      status: -1
    }
  },
  mounted () {
    window.addEventListener('keydown', this.keydown)
  },
  methods: {
    goToAdminHead: function () {
      let that = this
      let debug = false
      if (debug) {
        if (that.userName === 'admin' && that.userPassWord === '123456') {
          let loginInfo = {userName: 'admin', userNickName: '前端测试管理员'}
          that.cookie.setCookie(loginInfo)
          that.$router.push({
            name: 'AdminHead'
          })
        } else {
          that.$message.error('!!!')
        }
      } else {
        this.$http.request({
          url: that.$url + 'AdminLogin/',
          method: 'get',
          params: {
            // userNickName: that.userNickName,
            userName: that.userName,
            userPassWord: that.userPassWord
          }
        }).then(function (response) {
          console.log(response.data)
          that.status = response.data.ret
          if (that.status === 0) {
            let loginInfo = {userName: that.userName, userNickName: response.data.userNickName}
            that.cookie.setCookie(loginInfo)
            that.$router.push({
              name: 'AdminHead'
            })
          } else if (that.status === 1) {
            that.$message.error('管理员账号不存在')
          } else if (that.status === 2) {
            that.$message.error('密码错误')
          } else {
            that.$message.info('请输入管理员账号')
          }
        }).catch(function (error) {
          console.log(error)
        })
      }
    },
    goToStart: function () {
      this.$router.push({
        name: 'HelloWorld'
      })
    },
    keydown (e) {
      if (e.keyCode === 13) {
        this.goToAdminHead()
      }
    },
    destroyed () {
      window.removeEventListener('keydown', this.keydown, false)
    }
  }
}
</script>

<style scoped>
@import "../../assets/css/login.css";
@import "../../assets/css/Transition/head-login-register.css";
</style>



